html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}